





































import { Component, Vue } from 'vue-property-decorator'
import { mapGetters } from 'vuex'

@Component({
  computed: {
    ...mapGetters([
      'displayableErrors'
    ])
  }
})
export default class App extends Vue {
  name='App'
  isDark = true
  fetching = true

  created() {
    this.$store.dispatch('fetchCountries').finally(() => this.fetching = false)
  }
}
